.list-of-members {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.member {
  padding: 20px; }

.member-img {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background-position: center;
  background-size: cover; }
  @media only screen and (max-width: 768px) {
    .member-img {
      width: 200px;
      height: 200px; } }

.member-name {
  padding-top: 5px; }
